// __tests__/components/exercise-common/CategorySelector.test.js
import React from 'react';
import { render, fireEvent, act, waitFor } from '@testing-library/react-native';
import CategorySelector, { areEqual } from '../../../src/components/exercise-common/CategorySelector';

// ==========================
// ðŸ”¥ Mocks Animated
// ==========================
const mockStart = jest.fn((callback) => {
  if (callback) {
    // Simule fin dâ€™animation async
    setImmediate(() => {
      act(() => callback({ finished: true }));
    });
  }
});

const mockTiming = jest.fn(() => ({ start: mockStart }));
const mockParallel = jest.fn(() => ({ start: mockStart }));

const MockAnimatedValue = jest.fn().mockImplementation((initialValue) => ({
  _value: initialValue,
  interpolate: jest.fn(() => ({ _nativeTag: 1 })),
  setValue: jest.fn(),
  addListener: jest.fn(),
  removeListener: jest.fn(),
  removeAllListeners: jest.fn(),
  stopAnimation: jest.fn(),
  resetAnimation: jest.fn(),
}));

const mockMultiply = jest.fn(() => ({
  _nativeTag: 1,
  interpolate: jest.fn(() => ({ _nativeTag: 1 })),
}));

jest.mock('react-native', () => {
  const RN = jest.requireActual('react-native');
  return {
    ...RN,
    Animated: {
      ...RN.Animated,
      Value: MockAnimatedValue,
      timing: mockTiming,
      parallel: mockParallel,
      multiply: mockMultiply,
      View: RN.Animated.View,
    },
    ScrollView: RN.ScrollView,
    TouchableOpacity: RN.TouchableOpacity,
    Text: RN.Text,
    View: RN.View,
  };
});

// Mock LinearGradient
jest.mock('expo-linear-gradient', () => ({
  LinearGradient: ({ children, ...props }) => {
    const { View } = require('react-native');
    return (
      <View testID="linear-gradient" {...props}>
        {children}
      </View>
    );
  },
}));

// Mock styles
jest.mock('../../../src/components/exercise-common/CategorySelector/style', () => {
  const { StyleSheet } = require('react-native');
  return () =>
    StyleSheet.create({
      container: {},
      backgroundGradient: {},
      scrollView: {},
      scrollContent: {},
      categoryItemWrapper: {},
      categoryTouchable: {},
      selectedCategoryItem: {},
      selectedInner: {},
      selectedCategoryText: { color: '#FFFFFF' },
      sparkleContainer: {},
      sparkle: {},
      categoryItem: {},
      categoryInner: {},
      categoryText: {},
    });
});

// ==========================
// ðŸ”¹ DonnÃ©es de test
// ==========================
const categories = [
  { id: '1', name: 'Grammar' },
  { id: '2', name: 'Vocabulary' },
  { id: '3', name: 'Reading' },
];

// ==========================
// ðŸ”¹ Tests
// ==========================
describe('CategorySelector', () => {
  beforeEach(() => {
    jest.clearAllMocks();
  });

  // âœ… Rendu basique
  describe('Basic rendering', () => {
    it('renders all categories', () => {
      const { getByText } = render(
        <CategorySelector categories={categories} onSelectCategory={() => {}} />
      );
      expect(getByText('Grammar')).toBeTruthy();
      expect(getByText('Vocabulary')).toBeTruthy();
      expect(getByText('Reading')).toBeTruthy();
    });

    it('shows sparkle for selected category', () => {
      const { getByText } = render(
        <CategorySelector
          categories={categories}
          selectedCategory="1"
          onSelectCategory={() => {}}
        />
      );
      expect(getByText('âœ¨')).toBeTruthy();
    });
  });

  // âœ… Interactions
  describe('User interactions', () => {
    it('calls onSelectCategory when pressing category', async () => {
      const onSelectCategory = jest.fn();
      const { getByText } = render(
        <CategorySelector categories={categories} onSelectCategory={onSelectCategory} />
      );
      fireEvent.press(getByText('Vocabulary'));
      await waitFor(() => {
        expect(onSelectCategory).toHaveBeenCalledWith('2');
      });
    });

    it('does not call onSelectCategory if category is already selected', () => {
      const onSelectCategory = jest.fn();
      const { getByText } = render(
        <CategorySelector
          categories={categories}
          selectedCategory="1"
          onSelectCategory={onSelectCategory}
        />
      );
      fireEvent.press(getByText('Grammar'));
      expect(onSelectCategory).not.toHaveBeenCalled();
    });
  });

  // âœ… Animations
  describe('Animations', () => {
    it('triggers parallel animations when selectedCategory changes', async () => {
      const onSelectCategory = jest.fn();
      const { rerender } = render(
        <CategorySelector
          categories={categories}
          selectedCategory="1"
          onSelectCategory={onSelectCategory}
        />
      );

      mockTiming.mockClear();
      mockParallel.mockClear();

      await act(async () => {
        rerender(
          <CategorySelector
            categories={categories}
            selectedCategory="2"
            onSelectCategory={onSelectCategory}
          />
        );
      });

      await waitFor(() => {
        expect(mockParallel).toHaveBeenCalledTimes(1);
        expect(mockTiming).toHaveBeenCalledTimes(2);
      });
    });

    it('prevents animation conflicts with isAnimatingRef', async () => {
      const onSelectCategory = jest.fn();
      const { rerender } = render(
        <CategorySelector
          categories={categories}
          selectedCategory="1"
          onSelectCategory={onSelectCategory}
        />
      );

      mockParallel.mockClear();

      await act(async () => {
        rerender(
          <CategorySelector
            categories={categories}
            selectedCategory="2"
            onSelectCategory={onSelectCategory}
          />
        );
      });

      await act(async () => {
        rerender(
          <CategorySelector
            categories={categories}
            selectedCategory="3"
            onSelectCategory={onSelectCategory}
          />
        );
      });

      await waitFor(() => {
        expect(mockParallel).toHaveBeenCalledTimes(1);
      });
    });
  });

  // âœ… Gestion dynamique
  describe('Dynamic category management', () => {
    it('handles category addition', () => {
      const onSelectCategory = jest.fn();
      const { rerender } = render(
        <CategorySelector
          categories={categories}
          selectedCategory="1"
          onSelectCategory={onSelectCategory}
        />
      );

      expect(() =>
        rerender(
          <CategorySelector
            categories={[...categories, { id: '4', name: 'Writing' }]}
            selectedCategory="1"
            onSelectCategory={onSelectCategory}
          />
        )
      ).not.toThrow();
    });

    it('handles category removal', () => {
      const onSelectCategory = jest.fn();
      const { rerender } = render(
        <CategorySelector
          categories={categories}
          selectedCategory="1"
          onSelectCategory={onSelectCategory}
        />
      );

      expect(() =>
        rerender(
          <CategorySelector
            categories={[categories[0]]}
            selectedCategory="1"
            onSelectCategory={onSelectCategory}
          />
        )
      ).not.toThrow();
    });
  });

  // âœ… Memoisation
  describe('areEqual memoization', () => {
    it('returns true for identical props', () => {
      const props = {
        selectedCategory: '1',
        primaryColor: '#000',
        categories: [{ id: '1', name: 'Cat1' }],
      };
      expect(areEqual(props, props)).toBe(true);
    });
  });
});
